body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
input {
  height: 30px;
  position: absolute;
  top: 55%;
  left: 27%;
  -webkit-transform: translate(-50%, 50%);
      -ms-transform: translate(-50%, 50%);
          transform: translate(-50%, 50%);
  width: 300px;
  border: .5px solid grey;
  border-radius: 7px;
  background: lavender;
  text-indent: 5px;
}



